import {Registry} from 'cs-web-components-base';
import {prefixNS} from './helpers';

import Details from './details/Details';
import AttributeList from './details/components/AttributeList';
import LongText from './details/components/LongText';
import TaskContextTree from './details/components/TaskContextTree';
import TaskReferenceList from './details/components/TaskReferenceList';

import AppMenu from './main/AppMenu';
import DebugInfo from './main/DebugInfo';
import Main from './main/Main';
import TypeAheadBrowser from './main/browser/TypeAheadBrowser';

import TasksTable from './table/TasksTable';
import PrioCell from './table/cell_components/PrioCell';
import DateCell from './table/cell_components/DateCell';
import IconCell from './table/cell_components/IconCell';
import OverdueCell from './table/cell_components/OverdueCell';
import ProceedCell from './table/cell_components/ProceedCell';
import ReadStatusCell from './table/cell_components/ReadStatusCell';
import StatusCell from './table/cell_components/StatusCell';
import TagsCell from './table/cell_components/TagsCell';

import setupSagas from './sagas/sagas';
import setupDetailsSagas from './details/sagas/sagas';
import setupMainSagas from './main/sagas/sagas';
import setupTableSagas from './table/sagas/sagas';
import reducer from './reducers/reducers';

/* semi-public containers used in app configuration */
Registry.registerComponent(prefixNS('AppMenu'), AppMenu);
Registry.registerComponent(prefixNS('DebugInfo'), DebugInfo);
Registry.registerComponent(prefixNS('TasksTable'), TasksTable);
Registry.registerComponent(prefixNS('DetailsPane'), Details);
Registry.registerComponent(prefixNS('Main'), Main);

/* only registered for storybook */
Registry.registerComponent(prefixNS('TypeAheadBrowser'), TypeAheadBrowser);

/* public components for table cells */
Registry.registerComponent(prefixNS('DateCell'), DateCell);
Registry.registerComponent(prefixNS('IconCell'), IconCell);
Registry.registerComponent(prefixNS('OverdueCell'), OverdueCell);
Registry.registerComponent(prefixNS('PrioCell'), PrioCell);
Registry.registerComponent(prefixNS('ProceedCell'), ProceedCell);
Registry.registerComponent(prefixNS('ReadStatusCell'), ReadStatusCell);
Registry.registerComponent(prefixNS('StatusCell'), StatusCell);
Registry.registerComponent(prefixNS('TagsCell'), TagsCell);

/* public components for task details */
Registry.registerComponent(prefixNS('AttributeList'), AttributeList);
Registry.registerComponent(prefixNS('LongText'), LongText);
Registry.registerComponent(prefixNS('TaskContextTree'), TaskContextTree);
Registry.registerComponent(prefixNS('TaskReferenceList'), TaskReferenceList);

Registry.registerReducer(prefixNS('reducer'), reducer);
Registry.registerSaga(setupSagas);
Registry.registerSaga(setupMainSagas);
Registry.registerSaga(setupTableSagas);
Registry.registerSaga(setupDetailsSagas);

module.exports = [];  // eslint-disable-line no-undef



